/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
body.fijarbody {
  overflow: hidden;
}

main {
  flex: 1;
}

.contenedor {
  width: 95%;
  max-width: 1000px;
  margin: 0 auto;
}

[class$=contenedor] {
  width: 95%;
  max-width: 1000px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 3.5rem;
  font-family: "Montserrat", sans-serif;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  font-size: 2.9rem;
  font-family: "Montserrat", sans-serif;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.1rem;
  }
}

h3 {
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  h3 {
    font-size: 2.9rem;
  }
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-color: #d886e5;
}
.header__titulo {
  color: #FFFFFF;
}
.header__barra {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__titulo {
  font-weight: bold;
}
.header__contenido {
  display: flex;
  justify-content: flex-end;
}
.header__frase {
  text-align: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__frase {
    text-align: right;
    max-width: 50rem;
  }
}

.nav {
  display: flex;
  column-gap: 2rem;
  display: none;
}
.nav__a {
  color: #FFFFFF;
}
.nav__a:hover {
  text-transform: uppercase;
}
.nav i {
  margin: 0.5rem;
}
@media (min-width: 768px) {
  .nav {
    display: block;
  }
}

.menu i {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .menu {
    display: none;
  }
}

.contenedor-menu {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  top: 7.5rem;
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .contenedor-menu {
    display: none;
  }
}
.contenedor-menu a {
  text-align: center;
  display: block;
  color: #FFFFFF;
  padding: 2rem;
  background-color: #ff89ff;
  margin: 0.5rem 0;
  transition: background-color, color 0.4s ease-in-out;
}
.contenedor-menu a:hover {
  cursor: pointer;
  background-color: #ecc5f3;
  font-weight: bold;
}
.contenedor-menu a i {
  margin-right: 0.8rem;
}

.servicios__titulo {
  text-align: center;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .servicios__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.servicio {
  margin-top: 2rem;
  background-color: #ffd5ff;
  border-radius: 2rem;
  box-shadow: 24px 25px 15px -29px rgba(242, 82, 255, 0.6);
}
.servicio__mask {
  overflow: hidden;
}
.servicio__img {
  object-fit: cover;
}
.servicio__contenido {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.servicio__nombre, .servicio__precio {
  font-size: 2rem;
  margin: 0;
}
.servicio__nombre {
  font-weight: bold;
}
.servicio__precio {
  color: #d886e5;
  font-weight: bold;
}
.servicio__precio span {
  color: #000;
  font-weight: 300;
}
@media (min-width: 768px) {
  .servicio {
    margin-top: 0;
  }
}

.posicion__boton {
  display: flex;
  justify-content: flex-end;
}

.btn {
  background-color: #d886e5;
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #ca5cdc;
}
.btn.primario {
  padding: 1.5rem;
  border-radius: 2rem;
  font-size: 1.8rem;
}

.tramitar {
  margin: 2rem auto;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  padding: 3rem;
  background-image: linear-gradient(to bottom, #eed8f3 0%, #eed8f3 50%, transparent 51%, transparent 100%), url(../img/Corte1.jpg);
  background-size: 100%, 60rem;
  background-repeat: no-repeat;
  background-position: center, center bottom;
}
@media (min-width: 768px) {
  .tramitar {
    margin: 3rem auto;
    row-gap: 0;
    padding: 5rem;
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
    background-image: linear-gradient(to right, #eed8f3 0%, #eed8f3 50%, transparent 51%, transparent 100%), url(../img/Manicure2.jpg);
    background-size: 100%, 75rem;
    background-position: center, right center;
  }
}
.tramitar__contenido {
  padding: 1rem 2rem;
}
.tramitar__titulo {
  text-align: center;
}
@media (min-width: 768px) {
  .tramitar__titulo {
    text-align: left;
  }
}
.tramitar p {
  text-align: justify;
}
.tramitar__botones {
  display: grid;
  place-items: center;
}
.tramitar__btn {
  background-color: black;
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .tramitar__btn {
    width: auto;
  }
}
.tramitar__btn:hover {
  background-color: black;
}

.testimoniales__titulo {
  text-align: center;
}
@media (min-width: 480px) {
  .testimoniales__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.testimonial {
  background-color: #eed8f3;
}
.testimonial__grid {
  display: flex;
  justify-content: space-around;
  align-items: center;
  column-gap: 2rem;
}
.testimonial__nombre, .testimonial__comentario {
  margin: 0;
}